import { useState, useEffect, useContext, useCallback } from "react";
import { getSleepSummary } from "@/api/deviceReports";

import { percentageData } from "./mockData";
import Template from "@/Components/GraphAndChartTemp/Template";
import { getTemplateData } from "@/Pages/Elderlies/Components/Utiles/utiles";
import ScatterChart from "@/Components/GraphAndChart/scatterChart";
import { CustomContext } from "@/Context/UseCustomContext";
import dayjs from "dayjs";
import { transformDataForBedExit } from "@/utils/helper";

export default function DistributionOfBedExitTimes({ isSummaryBtn = true }) {
  const { title, color, icon, summaryProps, dataAnalysis } = getTemplateData(
    "Distribution of Bed Exit Times"
  );

  const context = useContext(CustomContext);
  const {
    chartToDate = "",
    chartFromDate = "",
    elderlyDetails,
  } = context || {};
  const [toDate, setToDate] = useState(
    dayjs().subtract(1, "day").format("YYYY-MM-DD")
  );
  const [fromDate, setFromDate] = useState(
    dayjs().subtract(31, "day").format("YYYY-MM-DD")
  );

  useEffect(() => {
    if (chartToDate && chartFromDate) {
      setToDate(chartToDate);
      setFromDate(chartFromDate);
    }
  }, [chartToDate]);

  const [bedExitDistribution, setBedExitDistribution] = useState([]);
  const getDailyRoutineData = useCallback(() => {
    getSleepSummary({
      uids: elderlyDetails?.bedRoomIds,
      elderly_id: elderlyDetails._id,
      to_date: fromDate,
      from_date: toDate,
    })
      .then((res) => {
        setBedExitDistribution(transformDataForBedExit(res.data || []));
      })
      .catch((err) => {
        console.log(err);
      });
  }, [toDate, elderlyDetails]);
  useEffect(() => {
    getDailyRoutineData();
  }, [getDailyRoutineData]);
  return (
    <Template
      title={title}
      color={color}
      icon={icon}
      dataAnalysis={dataAnalysis}
      isSummaryBtn={isSummaryBtn}
      summaryProps={summaryProps}
    >
      <ScatterChart
        data={bedExitDistribution}
        colors={{
          bedexit: color,
        }}
      />
    </Template>
  );
}
